import { Component, OnInit, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { IonModal, ModalController } from '@ionic/angular';
import { SearchComponent } from 'src/app/components/search/search.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';

// import Swiper core and required modules
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y, SwiperOptions } from 'swiper';
import { SwiperComponent } from 'swiper/angular';
import { DaterangePage } from '../daterange/daterange.page';
import { UnlockappPage } from '../unlockapp/unlockapp.page';
import { ActionSheetController, AlertController, Platform, MenuController } from '@ionic/angular';
import { OverlayEventDetail } from '@ionic/core/components';
import { AppComponent } from 'src/app/app.component';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})
export class DashboardPage implements OnInit {
  currentUser: any;
  @ViewChild('modal') modal: IonModal;
  @ViewChild(AppComponent) sidemenu!: AppComponent;
  @ViewChild('swiper', { static: false }) swiper?: SwiperComponent;
  config: SwiperOptions = {
    slidesPerView: 1,
    spaceBetween: 20,
    navigation: false,
    pagination: true,
    scrollbar: { draggable: true },
    centeredSlides: true,
  };
  slideIndex: number = 0;
  accountType: string;
  accountBalance: string;
  // firstName: string;
  phoneNumber: string;
  users: any;
  savingAccount: any;
  current: any;
  saving: any;
  image: any;
  profileData: any;
  loan: any;
  curr: string;
  currLoan: string;
  currCurrent: string;
  selectedAccountNumber: any;
  cards: any = [];
  isLoading: boolean = true;
  public menustatus: any;
  emailAddress: any;
  isFaceId: boolean = false;
  isFingerPrint: boolean = false;
  mpinActive: boolean = false;
  cardInfoInterval: any;
  isAndroid: boolean = false;
  customerId: any;

  constructor(private router: Router,
    private api: ApiService,
    public modalCtrl: ModalController,
    private sanitizer: DomSanitizer,
    private cdr: ChangeDetectorRef,
    private platform: Platform,
    public dataService: DataService,
    private alert: AlertController,
    public modalController: ModalController,
    public menu: MenuController) { }

  ngOnInit() {
    this.currentUser = this.dataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = localStorage.getItem('customer_id');
    let isOpenTray = localStorage.getItem('isShowed');

    this.isFaceId = this.currentUser?.faceLoginEnable ? true : false;
    this.isFingerPrint = this.currentUser?.fingerprintLoginEnable ? true : false;
    this.mpinActive = this.currentUser?.mpinActive ? true : false;

    this.platform.ready().then(() => {
      if (this.platform.is("android")) {
        this.isAndroid = true;
      }
      if (this.platform.is('ios') || this.platform.is("iphone") || this.platform.is("ipad")) {
        this.isAndroid = false;
      }
    });

    // install Swiper modules
    SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);
    localStorage.setItem('slideNo', '0');
    var customerDetails = JSON.parse(localStorage.getItem('customer_details'));
    this.getCustomerAccounts();
    console.log(customerDetails.mpin);
    localStorage.setItem('mpin', customerDetails.mpin);
    if (customerDetails.firstTimeLogin == "Y") {
      this.addCustomTask();
    }

    this.accountType = customerDetails.accountType;
    this.emailAddress = customerDetails.primaryEmailAdress;

    this.dataService.getAvatarUrl.subscribe((data) => {
      if (data != null) {
        this.image = data;
        this.cdr.markForCheck();
      }
    });

    setTimeout(() => {
      this.isLoading = false;
      if (this.platform.is("cordova")) {
        if (this.openUnlockTray() == true && (isOpenTray == "no" || isOpenTray == null || isOpenTray == undefined || isOpenTray == "")) {
          this.modal.present();
          localStorage.setItem('isShowed', "yes");
        }
      }
    }, 2000);

    this.cardInfoInterval = setInterval(() => this.getCustomerAccounts(), 10 * 1000);

    this.cdr.markForCheck();
  }

  getCustomerAccounts() {
    this.api.getCustomerAccounts(this.phoneNumber).subscribe(
      (resp: any) => {
        console.log('backend resp in home', resp);
        resp.custAccount = resp.custAccount.filter(
          (card: any) => card.status === 'APPROVED'
        );
        this.cards = resp.custAccount;
        localStorage.setItem('loginRespAccountId', this.cards?.[0]?.accountId);
        this.dataService.shareAccountInfo(this.cards[0]);
        this.getProfilePicture(this.customerId);
        this.cdr.markForCheck();
      }, (err: any) => {
        console.log(err)
      });
  }

  async addCustomTask() {
    let modal = await this.modalCtrl.create({
      component: UnlockappPage,
      cssClass: 'small-modal',
    });

    return await modal.present();
  }

  cancel() {
    this.modal.dismiss(null, 'cancel');
  }

  confirm() {
    this.modal.dismiss("", 'confirm');
  }

  onSwiper(i: any) {
    let index = i.realIndex;
    this.dataService.shareAccountInfo(this.cards[index]);
  }

  onSlideChange(i: any) {
    let index = i[0].realIndex;
    this.dataService.shareAccountInfo(this.cards[index]);
  }

  onWillDismiss(event: Event) {
    const ev = event as CustomEvent<OverlayEventDetail<string>>;
    if (ev.detail.role === 'confirm') {
      this.router.navigate(['/securitycenter']);
    }
  }

  openUnlockTray() {
    let isOpen: boolean = false;
    if (this.isAndroid) {
      if (this.mpinActive == true && this.isFingerPrint == true) {
        isOpen = false;
      } else {
        isOpen = true;
      }
    } else {
      if (this.mpinActive == true && this.isFaceId == true) {
        isOpen = false;
      } else {
        isOpen = true;
      }
    }
    return isOpen;
  }


  getProfilePicture(customerId) {
    const contentType = 'image/png';
    this.api.getProfileDetails(customerId).subscribe(
      (data: any) => {
        this.cdr.markForCheck();
        this.profileData = data;
        // console.log(' profile Image', this.profileData.profileImage.fileUrl);
        if (data?.profileImage != null) {
          let objectURL = data.profileImage;
          this.image =
            data.profileImage != 'not_available'
              ? this.sanitizer.bypassSecurityTrustUrl(objectURL)
              : undefined;
        } else {
          this.image = null;
        }
        this.cdr.markForCheck();
      },
      (error: any) => {
        console.log(error);
      }
    );
  }

  async searchModal() {
    const modal = await this.modalController.create({
      component: SearchComponent,
    });
    return await modal.present();
  }


  goToCashWithdrawal() {
    this.router.navigate(['cashwithdrawal']);
  }
  goToCashDeposit() {
    this.router.navigate(['cashdeposit']);
  }
  goToChequeDeposit() {
    this.router.navigate(['chequedeposit']);
  }
  goToChequeWithdrawal() {
    this.router.navigate(['chequewithdrawal']);
  }
  goToForexTransaction() {
    this.router.navigate(['forex-transaction']);
  }
  goToLoanPayment() {
    this.router.navigate(['loan-payment']);
  }
  goToUtilityPayment() {
    this.router.navigate(['utility-payment']);
  }
  goToDepositTopUp() {
    this.router.navigate(['deposit-topup']);
  }

  getAccountNumber(info) {
    console.log('number')
    this.dataService.shareAccountInfo(info);
    this.router.navigate(['/transaction']);
  }

  profilePage() {
    this.router.navigate(['profile'])
  }

  goToAccountSettings() {
    console.log('settings');
    this.router.navigate(['account']);
  }

  ionViewWillLeave() {
    clearInterval(this.cardInfoInterval);
  }
}
